import React, { useState } from 'react';
import Card from './Card';

const Personaje = (props)=> {

    const personajesInfo = [
        {personaje: 'Naruto Uzumaki',
        img: 'https://as.com/meristation/imagenes/2021/01/04/noticias/1609737165_936076_1609737340_noticia_normal.jpg',
        descripcion: 'es el protagonista de la historia. Un joven ninja que alberga en su interior al demonio Kyubi y que desea convertirse en Hokage de su aldea para demostrar a todos su verdadero poder, al igual que su valentía.'},
        {personaje: 'Sasuke Uchiha',
        img: 'https://cdn.superaficionados.com/imagenes/2-sasuke-signo-zodiacal-cke.jpg',
        descripcion: 'es un miembro del clan Uchiha. Es un chico serio, aunque posee gran habilidad para las artes ninja. Su objetivo es hacerse lo suficientemente fuerte como para derrotar a su hermano mayor Itachi, que asesinó a todo su clan'},
        {personaje: 'Sakura Haruno',
        img: 'https://cdn.alfabetajuega.com/wp-content/uploads/2021/02/sakura-naruto-abj.jpg',
        descripcion: 'es la única chica del equipo. Inicialmente está enamorada de Sasuke y desprecia a Naruto. Al avanzar la historia ve que Sasuke no tiene ningún interés en ella y empieza a apreciar a Naruto.'},
        {personaje: 'Kakashi Hatake', 
        img: 'https://i2.wp.com/myotakuworld.com/wp-content/uploads/2021/04/kakashi.jpg?resize=800%2C450&ssl=1',
        descripcion: 'es el primer maestro del equipo de Naruto, Sasuke y Sakura. Es un ninja experimentado, severo a primera vista y amante de las novelas para adultos, que se preocupa mucho por el trabajo en equipo.'},
        {personaje: 'Might Guy', 
        img: 'https://img.vixdata.io/pd/jpg-large/es/sites/default/files/m/might_guy_en_boruto_anime.jpeg',
        descripcion: 'Un cuerpo fuerte como el acero, y un Taijutsu que puede quebrar enormes piedras, pero ninguna de estas es la verdadera fuente de las fortalezas de Gai. Por el contrario, en su mirada constante hacia el frente, su eterno deseo de llegar a nuevas alturas.'},
        {personaje: 'Neji Hyūga',
        img: 'https://areajugones.sport.es/wp-content/uploads/2017/04/neji-keeps-on-fighting-hinata.jpg',
        descripcion: 'De pequeño, Neji era un niño alegre, dulce e inocente, no dándose cuenta del significado del Sello Maldito de la Familia Principal Hyūga hasta que su tío lo usó e hizo daño a su padre.'},
        {personaje: 'Tenten',
        img: 'https://cdn.alfabetajuega.com/wp-content/uploads/2018/10/alfabetajuega-bt-260418.jpg',
        descripcion: 'Tenten es muy sencilla, expresiva y espontánea. A pesar de esto, nunca falta el respeto a las demás personas, aunque muchas veces tiene pequeñas "meteduras de pata". Casi siempre se muestra alegre y procura ayudar tanto en la moral como en sus acciones a sus compañeros de equipo.'},
        {personaje: 'Rock Lee', 
        img: 'https://swall.teahub.io/photos/small/319-3196728_rock-lee.jpg',
        descripcion: 'Lee tiene un aspecto muy singular. A diferencia de la mayoría de los otros personajes de la serie, él posee cejas muy gruesas (que lleva los apodos de "cejas pobladas" o " cejas de azotador" por Naruto) y grandes ojos negros perfectamente redondos.'}
    ]

    const [ mostrar, setMostrar ] = useState(false);
    const [ mostrarMas, setMostrarMas ] = useState(false);

    const mostrarPersonajes = (event)=> {
        if(event){
            if(mostrar){
                setMostrar(false);
                setMostrarMas(false);
            } else {
                setMostrar(true);
            }
        }
    }
    const masPersonajes = (event)=> {
        if(event){
            if(mostrarMas){
                setMostrarMas(false);
            } else {
                setMostrarMas(true);
            }
        }
    }

    return(
        <div className="container fondoOpci mt-2 shadow">
            <button className="btn" onClick={mostrarPersonajes}>
                <h3 className="text-white">Personajes</h3></button>
            {
                mostrar && 
                    <div className="container">
                        <hr className="mt-0 " />
                        <p>{props.infoPersonajes}</p>
                        <button className="btn btn-outline-light mb-3" onClick={masPersonajes}>Ver los personajes</button>
                        {
                            mostrarMas && 
                                <div>
                                    <hr/>
                                    <div className="row container m-auto">
                                    {
                                        personajesInfo.map((e, index)=> 
                                        <Card personaje={e.personaje} 
                                              descripcion={e.descripcion}
                                              img={e.img} alt={e.personaje}
                                              key={index}/>)
                                    }
                                    </div>
                                </div>
                        }  
                    </div>
            }
        </div>
    );
}

export default Personaje;