import React, { useState } from 'react';
import '../../node_modules/bootstrap/dist/css/bootstrap.css';
import '../../node_modules/bootstrap/dist/js/bootstrap.bundle';

const Prueba = (props)=> {
    
    const [ info, setInfo ] = useState(false);
    const [ masInfo, setMasInfo ] = useState(false);

    const mostrarInfo = (event)=> {
        if(event){
            if(info){
                setInfo(false);
                setMasInfo(false);
            } else {
                setInfo(true);
            }
        }
    }
    const mostrarMasInfo = (event)=> {
        if(event){
            if(masInfo){
                setMasInfo(false);
            } else {
                setMasInfo(true);
            }
        }
    }

    return(
        <div className="container fondoOpci mt-2 shadow">
            <div>
                <button className="btn" onClick={mostrarInfo}>
                 <h2 className="text-white">Informacion de la serie</h2></button>
                {
                    info && 
                    <div className="container pb-3">
                        <hr className="mt-0"/>
                        <h6 className="mt-2">{props.informacion}</h6>
                        <button className="btn btn-outline-light mb-1" onClick={mostrarMasInfo}>Ver Mas</button>
                        {
                            masInfo &&
                            <div className="mb-2">
                                <hr/>
                                <p>{props.masInfo}</p>
                            </div>
                        }
                    </div>
                }
            </div>
        </div>
    );
}
export default Prueba;
