import React, { useState } from 'react';

const Musica = (props)=> {

    const Info = [
        {cancion: 'Naruto’s Theme', enlace: 'https://www.youtube.com/watch?v=daPo6q4FXDw'},
        {cancion: 'Sadness and Sorrow', enlace: 'https://www.youtube.com/watch?time_continue=1&v=-Ug070QK674'},
        {cancion: 'Orochimaru Fight', enlace: 'https://www.youtube.com/watch?v=C4N-Q14MvOI'},
        {cancion: 'Sasuke’s Theme', enlace: 'https://www.youtube.com/watch?time_continue=2&v=YKCaqDZFJ7o'},
        {cancion: 'Beautiful Green Wild Beast', enlace: 'https://www.youtube.com/watch?v=5C9QVGjvqj0'}
    ]
    const InfoDos = [
        {cancion: 'Naruto’s Daily Life', enlace: 'https://www.youtube.com/watch?v=AZeyNVms7jw'},
        {cancion: 'Dance', enlace: 'https://www.youtube.com/watch?v=LSvmpzDlDJU'},
        {cancion: 'Confrontment', enlace: 'https://www.youtube.com/watch?v=d57-85_Sfeg'},
        {cancion: 'Heavy Violence', enlace: 'https://www.youtube.com/watch?v=cRVj8yU_zdo'},
        {cancion: 'Strong and Strike', enlace: 'https://www.youtube.com/watch?v=0TsE01qdy5A'},
    ]

    const [ info, setInfo ] = useState(false);
    const [ canciones, setCanciones ] = useState(false);

    const mostrarInfo = (event)=> {
        if(event){
            if(info){
                setInfo(false);
                setCanciones(false);
            } else {
                setInfo(true);
            }
        }
    }
    const mostrarCanciones = (event)=> {
        if(event){
            if(canciones){
                setCanciones(false);
            } else {
                setCanciones(true);
            }
        }
    }

    return(
        <div className="container fondoOpci mt-2 shadow">
            <button className="btn" onClick={mostrarInfo}>
                <h3 className="text-white">Musica</h3></button>
            {
                info && 
                    <div className="container pb-3">
                        <hr className="mt-0"/>
                        <p>{props.infoMusica}</p>
                        <div>
                            <button className="btn btn-outline-light" onClick={mostrarCanciones}>Ver Canciones</button>
                            {
                                canciones && 
                                    <div>
                                        <hr/>
                                        <p>Entre ellas estas son las mas populares:</p>
                                        <div className="d-flex">
                                            <ol className="col-6">
                                                {
                                                    Info.map((e, index)=> 
                                                    <li key={index}>{e.cancion} (<a className="text-warning" href={e.enlace} target="_BLANK" rel="noreferrer">Escuchar...</a>)</li>)
                                                }
                                            </ol>
                                            <ol className="col-6" start="6">
                                                {
                                                    InfoDos.map((e, index)=> 
                                                    <li key={index}>{e.cancion} (<a className="text-warning" href={e.enlace} target="_BLANK" rel="noreferrer">Escuchar...</a>)</li>)
                                                }
                                            </ol>
                                        </div>
                                    </div>
                            }
                        </div>
                    </div>
            }
        </div>
    );
}
export default Musica;