import React, { useState } from 'react';

const Sumar = (props)=> {
    
    const [ info, setInfo ] = useState(false);
    const [ masInfo, setMasInfo ] = useState(false);

    const mostrarInfo = (event)=> {
        if(event){
            if(info){
                setInfo(false);
                setMasInfo(false);
            } else {
                setInfo(true);
            }
        }
    }
    const mostrarMasInfo = (event)=> {
        if(event){
            if(masInfo){
                setMasInfo(false);
            } else {
                setMasInfo(true);
            }
        }
    }

    return(
        <div className="container fondoOpci mt-2 shadow">
          <button className="btn" onClick={mostrarInfo}>
              <h3 className="text-white">Producción</h3></button>
          {
              info && 
                <div className="container pb-3">
                    <hr className="mt-0"/>
                    <p>{props.produccion}</p>
                    <div>
                        <button className="btn btn-outline-light mb-1" onClick={mostrarMasInfo}>Ver Mas</button>
                        {
                            masInfo && 
                            <div>
                                <hr/>
                                <p>{props.masInfoProd}</p>
                            </div>
                        }
                    </div>
                </div>
          }
        </div>
    );
}
export default Sumar;